.App {
  text-align: center;
  background-color: rgb(142, 241, 195);
  min-height: 100vh;
  
}

.chat_footer{
  bottom: 0px;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 62px;
  border: 1px solid lightgray;
  width: 100%;
  background-color: rgb(199, 233, 233);
  
  

}
.chat_footer> form{
  flex: 1;
  display: flex;
  
  
  
}
.chat_footer> form>input{
  
  flex: 1;
  border-radius: 30px;
  padding: 10px;
  border: 0.1px solid aquamarine;
}
.green >h1{
  color:rgb(142, 241, 195);
}
.chat_footer> form>button{
  border-radius: 10px;
}
.chat_footer> form>button:hover{
  cursor: pointer;
}
.green{
  
  padding-top: 100px
}

